@import url(https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap);
*{font-family:"Hammersmith One"}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:url("/blinguage/bg.jpg");background-size:65em;background-repeat:no-repeat;background-position:right top}button{border:2px solid black;border-radius:5px;background-color:#f2f5fe;font-size:inherit;padding:10px;margin-top:-11px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}li{list-style:none}.navbar{margin:1em 10em 0em 6em;display:flex;justify-content:space-between}.navbar-logo{margin-top:1em;width:10em;height:3em}.navbar-link{display:flex}.navbar-link li{margin:1em}.main{display:flex;flex-direction:column;margin-left:4.5em;margin-top:3em;font-size:1.4em}.main-title,.main-paraph{text-align:left}.main-paraph{width:20em}.main-btn{margin-top:1em;background-color:#372e7f;color:#e6dcbb;border:none;padding:1em;font-size:1rem}.details{display:flex;flex-direction:row;justify-content:space-around;margin:10em 20em 0em 5.5em}.details div{width:30rem}.detail{font-size:1.1em;display:flex;flex-direction:column;text-align:left}.detail p{width:20em}.detail-img{width:15%}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

